
import react, { Component } from 'react';
import { Link } from "react-router-dom";

export default function BreadCumbs(props) {

    var breadLis = props.path.split("/").map(function (cat, index) { return (<li><Link key={index}>{cat}</Link></li>); });

    return (
        <nav className='bread' aria-label="breadcrumb">
            <ol>
                {breadLis}
            </ol>
        </nav>
    )
}